'use strict';

/**
* Create an object from a provided list of properties and a provided list of corresponding values.
*
* @module @stdlib/array-base-zip2object
*
* @example
* var zip2object = require( '@stdlib/array-base-zip2object' );
*
* var props = [ 'a', 'b', 'c' ];
* var values = [ 1, 2, 3 ];
*
* var obj = zip2object( props, values );
* // returns { 'a': 1, 'b': 2, 'c': 3 }
*/

// MODULES //

var main = require( './main.js' );


// EXPORTS //

module.exports = main;
